@import url('./assets/css/Global.css');

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

::-webkit-scrollbar {
  width: 0%;
}

body {
  height: 0;
  background-color: var(--main-background);
}

header {
  display: block;
}

a {
  cursor: pointer;
}